import React, { Component } from "react";
import $ from "jquery";

export class AboutMe extends Component {
  componentDidMount() {

    this.slideTitle();
    this.slideContent();


  }
  hoverCTA() {
    document.getElementById("card--cta").classList.add("card--alt--hover");
  }
  
 
  
  hoverCTAOff() {
    document.getElementById("card--cta").classList.remove("card--alt--hover");
  }
  slideTitle(){
    var $animation_elements = $(".slide");
    var $window = $(window);
    function check_if_in_view() {
      var window_height = $window.height();
      var window_top_position = $window.scrollTop();
      var window_bottom_position = window_top_position + window_height;
      $.each($animation_elements, function () {
        var $element = $(this);
        var element_height = $element.outerHeight();
        var element_top_position = $element.offset().top;
        var element_bottom_position = element_top_position + element_height;
        if (
          element_bottom_position >= window_top_position &&
          element_top_position <= window_bottom_position
        )
          $element.addClass("hasSlid");
        else $element.removeClass("hasSlid");
      });
    }
    $window.on("scroll resize", check_if_in_view);


  }  
  animateLetters(){
    $( ".slide__letter1" ).css( "transform", "rotate(-290deg) scale(0.3)" );
    $( ".slide__letter2" ).css( "transform", "rotate(920deg) scale(0.5)" );
    $( ".slide__letter3" ).css( "transform", "rotate(1170deg) scale(0.5)" );
    $( ".slide__letter4" ).css( "transform", "rotate(320deg) scale(0.5)" );
    $( ".slide__letter5" ).css( "transform", "rotate(-330deg) scale(0.5)" );
    $( ".slide__letter6" ).css( "transform", "rotate(450deg) scale(0.5)" );
    $( ".slide__letter7" ).css( "transform", "rotate(420deg) scale(0.5)" );
 
  }
  animateLettersLeave(){
    $( ".slide__letter" ).css( "transform", "rotate(0) scale(1)" );

  }
  slideContent(){
    var $animation_elements = $(".slide__content");
    var $window = $(window);
    function check_if_in_view() {
      var window_height = $window.height();
      var window_top_position = $window.scrollTop();
      var window_bottom_position = window_top_position + window_height;
      $.each($animation_elements, function () {
        var $element = $(this);
        var element_height = $element.outerHeight();
        var element_top_position = $element.offset().top;
        var element_bottom_position = element_top_position + element_height;
        if (
          element_bottom_position >= window_top_position &&
          element_top_position <= window_bottom_position
        )
          $element.addClass("hasSlid");
        else $element.removeClass("hasSlid");
      });
    }
    $window.on("scroll resize", check_if_in_view);


  }
  render() {
    return (
      <section className="about" id="about">
        <h2 className="about__title slide" onMouseEnter={() => this.animateLetters()} onMouseLeave={() => this.animateLettersLeave()}><span className="slide__letter slide__letter1">A</span><span className="slide__letter slide__letter2">b</span><span className="slide__letter slide__letter3">o</span><span className="slide__letter slide__letter4">u</span><span className="slide__letter slide__letter5">t</span> <span className="slide__letter slide__letter6">m</span><span className="slide__letter slide__letter7">e</span></h2>
        <div className="about__content slide__content"     id="card--cta" onMouseEnter={() => this.hoverCTA()}
        onMouseLeave={() => this.hoverCTAOff()}>
          Hi! <br />I'm Dariusz and building pages is my hobby. I decided to change my
          career direction and follow my hobby - from being an automation
          engineer to being a web developer. I was polishing my web developing
          skills since last year, starting from HTML, CSS, pure JavaScript,
          through frameworks React, Angular and Vue.<br />I hope you find my projects
          interesting!
        </div>
      </section>
    );
  }
}
