import React, { Component } from "react";
import { MenuElement1 } from "./MenuComponents/MenuElement1";
import { MenuElement2 } from "./MenuComponents/MenuElement2";
import { MenuElement3 } from "./MenuComponents/MenuElement3";
import { MenuElement4 } from "./MenuComponents/MenuElement4";
import { SocialLink1 } from "./MenuComponents/SocialLink1";
import { SocialLink2 } from "./MenuComponents/SocialLink2";
import { SocialLink3 } from "./MenuComponents/SocialLink3";
import { SocialLink4 } from "./MenuComponents/SocialLink4";
import Logo from "../../Assets/db.svg";

export class Menu extends Component {
  render() {
    return (
      <section className="menu">
        <img className="menu__logo" src={Logo} href="/" />
        <div className="menus">
        <ul className="menus__list">
          <MenuElement1 />
          <MenuElement2 />
          <MenuElement3 />
          <MenuElement4 />
        </ul>
        <ul className="menus__list">
          <SocialLink1 />
          <SocialLink2 />
          <SocialLink3 />
          <SocialLink4 />
        </ul>
        </div>
      </section>
    );
  }
}
