import React, { Component } from "react";
import $ from "jquery";

export class Skills extends Component {
  componentDidMount() {

    this.slideTitle();
    this.slideContent();


  }

  
 
  

  slideTitle(){
    var $animation_elements = $(".slide");
    var $window = $(window);
    function check_if_in_view() {
      var window_height = $window.height();
      var window_top_position = $window.scrollTop();
      var window_bottom_position = window_top_position + window_height;
      $.each($animation_elements, function () {
        var $element = $(this);
        var element_height = $element.outerHeight();
        var element_top_position = $element.offset().top;
        var element_bottom_position = element_top_position + element_height;
        if (
          element_bottom_position >= window_top_position &&
          element_top_position <= window_bottom_position
        )
          $element.addClass("hasSlid");
        else $element.removeClass("hasSlid");
      });
    }
    $window.on("scroll resize", check_if_in_view);


  }  
  hoverCTA() {
    document.getElementById("card--cta").classList.add("card--alt--hover");
  }
  
 
  
  hoverCTAOff() {
    document.getElementById("card--cta").classList.remove("card--alt--hover");
  }
  animateLetters(){
    
    $( ".slide-skills__letter1" ).css( "transform", "rotate(920deg) scale(0.5) translateX(4px)" );
    $( ".slide-skills__letter2" ).css( "transform", "rotate(920deg) scale(0.5)" );
    $( ".slide-skills__letter3" ).css( "transform", "rotate(1170deg) scale(0.5)" );
    $( ".slide-skills__letter4" ).css( "transform", "rotate(320deg) scale(0.5)" );
    $( ".slide-skills__letter5" ).css( "transform", "rotate(-330deg) scale(0.5)" );
    $( ".slide-skills__letter6" ).css( "transform", "rotate(450deg) scale(0.5) translateX(4px)" );
 
  }
  animateLettersLeave(){
    $( ".slide-skills__letter" ).css( "transform", "rotate(0) scale(1)" );

  }
  slideContent(){
    var $animation_elements = $(".slide__content");
    var $window = $(window);
    function check_if_in_view() {
      var window_height = $window.height();
      var window_top_position = $window.scrollTop();
      var window_bottom_position = window_top_position + window_height;
      $.each($animation_elements, function () {
        var $element = $(this);
        var element_height = $element.outerHeight();
        var element_top_position = $element.offset().top;
        var element_bottom_position = element_top_position + element_height;
        if (
          element_bottom_position >= window_top_position &&
          element_top_position <= window_bottom_position
        )
          $element.addClass("hasSlid");
        else $element.removeClass("hasSlid");
      });
    }
    $window.on("scroll resize", check_if_in_view);


  }
  render() {
    return (
      <section className="skills" id="skills">
        <h2 className="about__title slide" onMouseEnter={() => this.animateLetters()} onMouseLeave={() => this.animateLettersLeave()}><span className="slide-skills__letter  slide-skills__letter1">S</span><span className="slide-skills__letter slide-skills__letter2">k</span><span className="slide-skills__letter slide-skills__letter3">i</span><span className="slide-skills__letter slide-skills__letter4">l</span><span className="slide-skills__letter slide-skills__letter5">l</span><span className="slide-skills__letter slide-skills__letter6">s</span></h2>
        <div className="about__content slide__content"  >
 
        </div>
      </section>
    );
  }
}
